import React from "react"
import "../../assets/css/product-sl.css"
import ImageCarousel from "./Components/ImageCarousel/BDImageCarousel"
import ReviewBar from "./Components/Review/ReviewBar"
import Attributes from "./Components/Attributes/BDAttributes"
import AddToCartBox from "./Components/AddToCart/BDAddToCartBox"
import AddedToCartModal from "./Components/AddToCart/Components/AddedToCartModal"
import Designer from "./Components/Designer/BDDesigner"
import Reviews from "./Components/Review/components/Reviews"

import htmldecoder from "../../functions/htmldecoder"

import CategoryBreadcrumb from "../AC-Breadcrumb/BDCategoryBreadcrumb"
import classes from "./Styles/BDProductPage.module.css"
import ProductInfoTabs from "./Components/ProductInfo/ProductInfoTabs"

const ProductPage = ({ data }) => {
  console.error("DATADATA", data)
  return (
    <React.Fragment>
      <div className={classes.itemPage}>
        <CategoryBreadcrumb />

        <div className={classes.itemMain}>
          <ImageCarousel
            giftcard="false"
            data={{
              productCode: data.productData._xResult[0].code,
              hiddenProperties: data.productData._xResult[0].hiddenProperties,
              title: data.productData._xResult[0].title,
              url: data.url,
            }}
          />
          <div>
            <div className={classes.mobileHeader}>
              <h2 className={classes.itemTitle}>
                {htmldecoder(data.productData._xResult[0].title)}
              </h2>
              <h1
                className={classes.shortDesc}
                dangerouslySetInnerHTML={{
                  __html: htmldecoder(data.productData._xResult[0].shortdesc),
                }}
              />
            </div>
            <div className={classes.supplierName}>
              <Designer supplier={data.supplierData._xResult[0]} />
              <ReviewBar
                avgRating={
                  data.supplierData._xResult[0].distributorOrder[0].rating
                }
              />
            </div>

            <AddToCartBox
              giftcard="false"
              priceInv={data.priceInvData._xResult[0]}
            />

            <Attributes />

            <ProductInfoTabs
              distIdState={data.supplierData._xResult[0].distributorOrder[0].desc.toLowerCase()}
              itemDetailState={data.productData._xResult[0].longdesc}
              itemPropertiesState={data.productData._xResult[0].properties}
            />
            <AddedToCartModal />
          </div>
        </div>
        <Reviews avgRating={0} setAvgRating={() => {}} />
      </div>
    </React.Fragment>
  )
}

export default ProductPage
