import React, { useState, useEffect, useContext } from "react"
import { useSelector, shallowEqual } from "react-redux"
import { PREVIEW, PROJECT_LINK } from "../../../../project-config.js"
import { SideBySideMagnifier } from "react-image-magnifiers"

import "./Styles/ImageCarousel.css"
import { I18nContext } from "../../../../i18n/index.js"

import Measurement from "../Measurement/Measurement"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"

// import { classes } from "istanbul-lib-coverage";
import classes from "./Styles/BDImageCarousel.module.css"
import {
  TwitterShareButton,
  FacebookShareButton,
  EmailShareButton,
  LineShareButton,
  LinkedinShareButton,
  PinterestShareButton,
  WhatsappShareButton,
  WeiboShareButton,
  TwitterIcon,
  FacebookIcon,
  EmailIcon,
  LineIcon,
  LinkedinIcon,
  PinterestIcon,
  WeiboIcon,
  WhatsappIcon,
} from "react-share"

const ImageCarousel = props => {
  const { translate, langCode } = useContext(I18nContext)

  const [mainImage, setMainImage] = useState("")
  const [mainImageUrl, setMainImageUrl] = useState(
    "https://bdadmin3qa.avetti.ca/preview/store/20180522154/assets/items/largeimages/"
  )
  const [additionalImages, setAdditionalImages] = useState(0)
  const [imageRatios, setImageRatios] = useState("")
  const [productUrl, setProductUrl] = useState("")

  const productInitialState = useSelector(
    state => state.productReducer.product,
    shallowEqual
  )

  const productTempState = useSelector(
    state => state.productReducer.productInitial,
    shallowEqual
  )
  console.info("kakka", productTempState)

  const productCode = useSelector(
    state => state.productReducer.itemDetail.code,
    shallowEqual
  )

  const loadingState = useSelector(
    state => state.productReducer.loading,
    shallowEqual
  )

  const isMobileState = useSelector(
    state => state.mainReducer.isMobile,
    shallowEqual
  )

  const propertiesState = useSelector(
    state => state.productReducer.itemDetail.properties
  )

  const itemDetailState = useSelector(
    state => state.productReducer.itemDetail,
    shallowEqual
  )

  console.log("ITEM DETAIL STATE", itemDetailState)

  useEffect(() => {
    let url = productInitialState.url.replace("product/", "")
    console.info("url", url)
    let langCodePart = langCode == "en" ? "" : `/${langCode}`
    // console.log("PRODUCT URL", window.location.origin + PREVIEW + langCodePart + "/" + url)
    // setProductUrl(window.location.origin + PREVIEW + langCodePart + "/" + url);
    setProductUrl(window.location.href)
  }, [])

  useEffect(() => {
    if (!loadingState && mainImage) {
      const image = () => {
        let img = new Image()
        img.src = `${mainImageUrl}${mainImage}.jpg`
        setImageRatios({
          width: img.naturalWidth,
          height: img.naturalHeight,
        })
      }
    }
  }, [])

  console.log("IMAGE RATIOS", imageRatios)

  useEffect(() => {}, [itemDetailState])

  useEffect(() => {
    setMainImage(props.data.productCode)

    let hiddenProps = props.data.hiddenProperties
    console.error("HELLO FROM CAROUSEL", hiddenProps, props.data)
    if (hiddenProps != undefined && hiddenProps.length > 0) {
      let prop = hiddenProps.find(num => {
        return num.propname == "Sys_Num_Images"
      })
      if (parseInt(prop.propvalue) > 1) {
        setAdditionalImages(parseInt(prop.propvalue))
      }
    }
  }, [props])

  const handleImageChange = (img, condition) => {
    console.log("CLICKED CAROUSEL IMG: ", img)

    setMainImage(img)

    if (condition) {
      setMainImageUrl(
        "https://bdadmin3qa.avetti.ca/preview/store/20180521148/assets/images/galleries/"
      )
    } else {
      setMainImageUrl(
        "https://bdadmin3qa.avetti.ca/preview/store/20180522154/assets/items/largeimages/"
      )
    }
  }

  if (!loadingState && mainImage) {
    return (
      <div>
        {/* <button onClick={image}>CLICK ME</button> */}
        <div>
          {/* <ReactImageMagnify
            {...{
              smallImage: {
                alt: productInitialState.title,
                isFluidWidth: true,
                src: `${mainImageUrl}${mainImage}.jpg`,
                //src: image,
                // width: "100%",
                // height: "100%"
                // sizes: "(max-width: 480px) 100vw, (max-width: 1200px) 34vw, 360px"
              },
              largeImage: {
                src: `${mainImageUrl}${mainImage}.jpg`,
                width: 1000,
                height: 1500
              },
              enlargedImageContainerDimensions: {
                width: isMobileState ? `100%` : "100%",
                height: isMobileState ? `100%` : "100%"
              },
              isHintEnabled: true,
              // enlargedImageClassName: "magnified-image",
              // enlargedImageContainerClassName: classes.magnifiedImageContainer
              // enlargedImagePosition: "over"
            }}
          /> */}

          <SideBySideMagnifier
            className="boraaa"
            imageSrc={`https://bdadmin3qa.avetti.ca/preview/store/20180522154/assets/items/images/${mainImage}.jpg`}
            imageAlt="Example"
            largeImageSrc={`https://bdadmin3qa.avetti.ca/preview/store/20180522154/assets/items/largeimages/${mainImage}.jpg`} // Optional
            // mouseActivation={MOUSE_ACTIVATION.CLICK} // Optional
            // touchActivation={TOUCH_ACTIVATION.DOUBLE_TAP} // Optional
          />

          {/* <MagnifierContainer>
            <div className="example-class">
              <MagnifierPreview imageSrc={`https://bdadmin3qa.avetti.ca/preview/store/20180522154/assets/items/images/${mainImage}.jpg`} />
              <MagnifierZoom style={{ height: "400px" }} imageSrc={`https://bdadmin3qa.avetti.ca/preview/store/20180522154/assets/items/largeimages/${mainImage}.jpg`} />

            </div>
          </MagnifierContainer> */}
        </div>

        <div id="product_carousel">
          <div className={classes.app}>
            <ul className={`${classes.hs} ${classes.full}`}>
              <li
                className={classes.item}
                style={{
                  background: `url(https://bdadmin3qa.avetti.ca/preview/store/20180522154/assets/items/thumbnails/${props.data.productCode}.jpg)`,
                }}
              >
                {/* <img
                  className={classes.carouselThumb}
                  src={`https://bdadmin3qa.avetti.ca/preview/store/20180522154/assets/items/thumbnails/${productCode}.jpg`}
                  alt=""
                 
                  onClick={() => handleImageChange(productCode)}
                /> */}
              </li>

              {additionalImages > 0
                ? [...Array(additionalImages)].map((e, i) => {
                    if (i > 1 && i != 3) {
                      return (
                        // ? additionalImages.map((img, index) => (
                        <li
                          className={classes.item}
                          style={{
                            background: `url(https://bdadmin3qa.avetti.ca/preview/store/20180522154/assets/items/thumbnails/${props.data.productCode}-${i}.jpg)`,
                          }}
                          onClick={() =>
                            handleImageChange(`${props.data.productCode}-${i}`)
                          }
                        >
                          {/* <img
                      className={classes.carouselThumb}
                      src={`https://bdadmin3qa.avetti.ca/preview/store/20180522154/assets/items/thumbnails/${productCode}-${i}.jpg`}
                      alt=""
                    
                      onClick={() => handleImageChange(`${productCode}-${i}`)}
                    /> */}
                        </li>
                      )
                    }
                  })
                : null}
            </ul>
          </div>
        </div>

        <div className={classes.shareWrapper}>
          <div className={classes.innerShareWrap}>
            <div className={classes.shareText}>SHARE THIS ITEM</div>
            <div
              style={{
                display: "flex",
                flexWrap: "wrap",
              }}
              className={classes.iconsWrapper}
            >
              <FacebookShareButton
                className={classes.shareButton}
                url={props.data.url}
                quote={`This custom made dress is a super fun, ${props.data.title}, THE MOST BEAUTIFUL CUSTOM CLOTHES IN THE WORLD.`}
                hashtags={["Balodana", "Style", "Fashion"]}
              >
                {/* <FacebookIcon size={25} round={true} /> */}
                <FontAwesomeIcon
                  className={classes.shareIcon}
                  icon={["fab", "facebook-f"]}
                />
              </FacebookShareButton>

              <TwitterShareButton
                className={classes.shareButton}
                url={props.data.url}
                title={`This custom made dress is a super fun, ${props.data.title}, THE MOST BEAUTIFUL CUSTOM CLOTHES IN THE WORLD.`}
                hashtags={["Balodana", "Style", "Fashion"]}
              >
                {/* <TwitterIcon size={25} round={true} /> */}
                <FontAwesomeIcon
                  className={classes.shareIcon}
                  icon={["fab", "twitter"]}
                />
              </TwitterShareButton>
              <div className={classes.shareButton}>
                <a
                  target="_blank"
                  href="https://www.instagram.com/balodana/?hl=en"
                >
                  <FontAwesomeIcon
                    className={classes.shareIcon}
                    icon={["fab", "instagram"]}
                  />
                </a>
              </div>

              <PinterestShareButton
                className={classes.shareButton}
                url={props.data.url}
                description={`This custom made dress is a super fun, ${props.data.title}, THE MOST BEAUTIFUL CUSTOM CLOTHES IN THE WORLD.`}
                media={`https://bdadmin3qa.avetti.ca/preview/store/20180522154/assets/items/largeimages/${mainImage}.jpg`}
              >
                {/* <PinterestIcon size={25} round={true} /> */}
                <FontAwesomeIcon
                  className={classes.shareIcon}
                  icon={["fab", "pinterest-p"]}
                />
              </PinterestShareButton>
            </div>
          </div>
          {props.giftcard === "true" ? null : <Measurement />}
        </div>
      </div>
    )
  } else {
    return (
      <div id="imageGrid">
        <div className="preview" id="item-img">
          <span id="itemImg22" style={{ display: "inline-block" }}>
            <img
              id="js-item-image-267578"
              name="slika"
              src={productInitialState.image}
              className="img-responsive itemMainImg"
              style={{
                cursor: "pointer",
                float: "left",
                border: "1px solid #333",
              }}
              title={productInitialState.title}
            />
          </span>
        </div>
      </div>
    )
  }
}

export default ImageCarousel
