import React from "react"
import { useSelector, shallowEqual, useDispatch } from "react-redux"
import {
  getDeliveryOptions,
  addToLocalMiniCart,
} from "../../../../redux/actions/productAction"
import EnquiryModal from "./Components/Enquiry/EnquiryModal.jsx"
import { I18nContext } from "../../../../i18n/index"
import { FormattedNumber } from "react-intl"
import WishListBar from "../WishList/BDWishListBar"
import "./Styles/AddToCartBox.css"
import classes from "./Styles/BDAddToCartBox.module.css"

const AddToCartBox = props => {
  const dispatch = useDispatch()
  const { translate, currency, priceConvert } = React.useContext(I18nContext)

  const [listPrice, setListPrice] = React.useState(
    props.priceInv.prices[0].listprice
  )
  const [price, setPrice] = React.useState(props.priceInv.prices[0].price_1)
  const [numberOfItems, setNumberOfItems] = React.useState(1)
  const [supplierName, setSupplierName] = React.useState("")
  const [supplierCity, setSupplierCity] = React.useState("")
  const [enquiryModalState, setEnquiryModalState] = React.useState(false)

  const priceState = useSelector(
    state => state.productReducer.priceInventory,
    shallowEqual
  )

  const mainItemIdState = useSelector(
    state => state.productReducer.itemDetail.mainitemid,
    shallowEqual
  )

  const selectedProductAttributesState = useSelector(
    state => state.productReducer.selectedProductAttributes,
    shallowEqual
  )

  const skuPriceState = useSelector(
    state => state.productReducer.skuPrice,
    shallowEqual
  )
  const currencyState = useSelector(
    state => state.mainReducer.currency,
    shallowEqual
  )
  const supplierInfoReducer = useSelector(
    state => state.productReducer.supplierInfo,
    shallowEqual
  )
  const productInitialStateFromFetchTitle = useSelector(
    state => state.productReducer.productInitial.title,
    shallowEqual
  )

  const loginNameState = useSelector(
    state => state.loginReducer.loginName,
    shallowEqual
  )

  /*   React.useEffect(() => {
    if (Object.keys(skuPriceState).length > 0) {
      if (skuPriceState.prices[0].listprice > 0) {
        setListPrice(skuPriceState.prices[0].listprice)
      }
      setPrice(skuPriceState.prices[0].price_1)
    } else if (Object.keys(priceState).length > 0) {
      if (priceState.prices[0].listprice > 0) {
        setListPrice(priceState.prices[0].listprice)
      }
      setPrice(priceState.prices[0].price_1)
    }
  }, [priceState, skuPriceState]) */

  React.useEffect(() => {
    if (supplierInfoReducer.length > 0) {
      if (supplierInfoReducer[0].distributorOrder.length > 0) {
        setSupplierName(supplierInfoReducer[0].distributorOrder[0].name)
        setSupplierCity(supplierInfoReducer[0].distributorOrder[0].city)
      }
    }
  }, [supplierInfoReducer])

  const handleAddToCart = () => {
    let attributesObject = null
    if (
      mainItemIdState &&
      mainItemIdState !== 0 &&
      selectedProductAttributesState
    ) {
      if (selectedProductAttributesState.hasOwnProperty(mainItemIdState)) {
        attributesObject = selectedProductAttributesState[mainItemIdState]
      }
    }
    /*   console.info(
      "attributesObject",
      mainItemIdState,
      selectedProductAttributesState,
      attributesObject
    ); */
    dispatch(
      getDeliveryOptions(
        priceState.prices[0].distributorId,
        priceState.code,
        numberOfItems,
        priceState.itemid,
        attributesObject
      )
    )
    dispatch(
      addToLocalMiniCart(
        productInitialStateFromFetchTitle,
        price,
        numberOfItems,
        supplierName
      )
    )
  }

  const handleEnquiryModalOpenClicked = () => {
    setEnquiryModalState(true)
  }

  return (
    <React.Fragment>
      <EnquiryModal
        isLoggedIn={loginNameState !== ""}
        enquiryModalState={enquiryModalState}
        setEnquiryModalState={setEnquiryModalState}
      />

      <div className={classes.price}>
        {listPrice > 0 ? (
          <div className={classes.listPrice}>
            <FormattedNumber
              value={listPrice}
              style="currency"
              currency="USD"
            />
          </div>
        ) : null}
        <div>
          <FormattedNumber value={price} style="currency" currency="USD" />
        </div>
      </div>

      <div className={classes.quantityRow}>
        <span className={classes.qtyText}>QUANTITY</span>
        <div id="buyBoxQtyBox">
          <div className="">
            <div className={classes.qtyBox}>
              <div
                className={classes.qtyControl}
                onClick={() => {
                  if (numberOfItems - 1 > 0) {
                    setNumberOfItems(numberOfItems - 1)
                  }
                }}
                style={{ cursor: "pointer" }}
              >
                <span>-</span>
              </div>
              <input
                id="qtybox-dist-204"
                className={classes.qtyInput}
                type="text"
                value={numberOfItems}
                onChange={e => setNumberOfItems(Number(e.target.value))}
              />
              <div
                className={classes.qtyControl}
                onClick={() => setNumberOfItems(numberOfItems + 1)}
                style={{ cursor: "pointer" }}
              >
                <span>+</span>
              </div>
            </div>

            {/* <div className="clearfix"></div> */}
          </div>
        </div>
      </div>

      <div id="buyBoxAddToCartBtn">
        <div
          className={classes.addToCartBtn}
          title={translate("js.item.addtocart")}
          onClick={() => handleAddToCart()}
        >
          ADD TO CART
        </div>
        {props.giftcard === "true" ? null : (
          <div className={classes.flexWrapper}>
            <div id="buyBoxEnquiryBtn">
              <div
                id="enquiry-204"
                className={classes.enquiry}
                title={translate("js.item.enquiry")}
                onClick={handleEnquiryModalOpenClicked}
              >
                <i className="material-icons add-icon">mail_outline</i>
                <span>Contact Designer</span>
              </div>
            </div>

            <WishListBar />
          </div>
        )}
      </div>
    </React.Fragment>
  )
}

export default AddToCartBox
