import React from "react"
import { useDispatch, useSelector, shallowEqual } from "react-redux"
import { setReviewModalStateAction } from "../../../../redux/actions/productAction"

import { I18nContext } from "../../../../i18n/index"
import { WriteReviewModal } from "./components/WriteReviewModal"
import productReducer from "../../../../redux/reducers/productReducer"

import classes from "./Styles/ReviewBar.module.css"

const ReviewBar = ({ avgRating }) => {
  // console.info("avgRating2", avgRating.avgRating);
  const dispatch = useDispatch()

  const { translate } = React.useContext(I18nContext)

  const reviewsState = useSelector(
    state => state.productReducer.productInitial.reviews,
    shallowEqual
  )

  const renderRatingStars = rating => {
    const renderStarBasedOnAvgRating = i => {
      if (i <= avgRating) return `star`
      else if (i - 0.5 == avgRating) return `star_half`
      else return `star_border`
    }
    return (
      <div>
        {[...Array(5)].map((_, i) => (
          <i key={i} className="material-icons">
            {renderStarBasedOnAvgRating(i + 1)}
          </i>
        ))}
      </div>
    )
  }

  if (reviewsState && reviewsState.length > 0) {
    return (
      <React.Fragment>
        <WriteReviewModal />
        <div id="reviewGrid" className={classes.reviewGrid}>
          <div className={classes.reviewStars}>
            {renderRatingStars(avgRating)}
          </div>
          {/* <div className="review-points" style={{ padding: "5px 0 0 5px" }}>
            {` ${avgRating.avgRating} `}
            <span>
              {translate("js.item.review.outof")} 5{" "}
              {`( ${reviewsState.length} )`}
            </span>
          </div> */}
          <div className={classes.writeReview}>
            {/* <a
              href="#reviewsContainer"
              id="review-link"
              style={{ paddingLeft: "5px", paddingRight: "5px" }}
            >
              <span>READ REVIEWS</span>
            </a>
            <span>|</span> */}
            <a onClick={() => dispatch(setReviewModalStateAction(true))}>
              <span>WRITE A REVIEW</span>
            </a>
          </div>
        </div>
      </React.Fragment>
    )
  } else {
    return (
      <React.Fragment>
        <WriteReviewModal />
        <div id="reviewGrid" className={classes.reviewGrid}>
          <div className={classes.writeReview}>
            <a onClick={() => dispatch(setReviewModalStateAction(true))}>
              <span>WRITE A REVIEW</span>
            </a>
          </div>
        </div>
      </React.Fragment>
    )
  }
}

export default ReviewBar
