import React, { useState, useEffect } from "react"
import { useSelector, shallowEqual } from "react-redux"
import Needle from "../../../../assets/img/balodana/itemDetails/needle.png"
import classes from "./Styles/BDDesigner.module.css"
import { Link } from "gatsby"
import { PREVIEW } from "../../../../project-config.js"

const Designer = ({ supplier }) => {
  const [supplierName, setSupplierName] = useState(
    supplier.distributorOrder[0].name
  )

  return (
    <Link
      to={
        PREVIEW +
        "/designer/" +
        supplierName
          .toLowerCase()
          .split(" ")
          .join("-")
      }
    >
      <div className={classes.designer}>
        <img src={Needle} alt="Needle Icon" />
        <span className={classes.name}>by {supplierName.toUpperCase()}</span>
      </div>
    </Link>
  )
}

export default Designer
